@import url('https://fonts.googleapis.com/css2?family=DM+Sans:ital,opsz,wght@0,9..40,100..1000;1,9..40,100..1000&family=Ubuntu:wght@300&display=swap');

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Ubuntu', sans-serif;
}

.App {
  display: flex;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.SideBar {
  width: 300px;
  min-width: 210px;
  height: 100%;
}

.Main {
  width: 100%;
  height:100%;
  background: url('./images/pic1.jpg') no-repeat;
  background-size: cover;
  position: relative;
}

.User {
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 1rem;
  border: 1px solid #ebebeb;
}

.User .logo img {
  width: 50px;
  border-radius: 50px;
  border: 1px solid #000;
}

.User .info p {
  text-align: center;
  font-size: 1.1rem;
  font-weight: bold;

}

.User .info a {
  text-decoration: none;
  display: flex;
  justify-content: space-around;
}

.AddNewTodo {
  border-bottom: 1px solid #ebebeb;
}
.AddNewTodo .btn {
  padding: 1rem;
  width: 100%;
  height: 70px;
}
.AddNewTodo .btn button {
  width: 100%;
  height: 100%;
  border: none;
  border-radius: 3px;
  font-size: 1rem;
  background-color: #0080ff;
  color: white;
  font-weight: bold;
  cursor: pointer;
}
.AddNewTodo .btn button:hover {
  filter: brightness(1.1);
  /*При наведении на кнопку (:hover) все изменения свойств (в данном случае filter)
  будут анимироваться в течение 300 миллисекунд с плавным ускорением и замедлением.*/
  transition: all 300ms ease;
}

.Modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0,0,0, 0.8);
  z-index: 99;
}
.Modal .container {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -25%);
}

.TodoForm {
  padding: 1rem 1rem calc(1rem + 40px) 1rem;
  width: 100%;
  background: white;
  border-radius: 4px;
}

.TodoForm .text input {
  padding: 1rem;
  width: 100%;
  margin-bottom: 0.5rem;
  border: none;
  font-size: 1.2rem;
}
.TodoForm .text input:focus {
  outline: none;
  border-bottom: 1px solid #0080ff;
}

.TodoForm .remind {
  display: flex;
  align-items: center;
  padding: 0.5rem;
}
.TodoForm p {
  margin-left: 10px;
}
.TodoForm .title {
  display: flex;
  align-items: center;
  margin-bottom: 5px;
}
.TodoForm .pick-day {
  padding: 1.5rem 1.5rem 0 1.5rem;
}
.TodoForm .pick-time {
  padding: 1.5rem;
}
.TodoForm .pick-project {
  padding: 0.5rem;
}
.TodoForm .pick-project .projects {
  display: flex;
  flex-wrap: wrap;
  padding: 1rem 1rem 0 1rem;
}
.TodoForm .pick-project .project {
  border: 1px solid #0080ff;
  border-radius: 5px;
  padding: 5px;
  margin: 5px;
  cursor: pointer;
}
.AddNewTodo form .pick-project .projects .active {
  background-color: #0080ff;
  color: white;
}

.EditTodo form .pick-project .projects .active {
  background-color: #0080ff;
  color: white;
}
.AddNewTodo form .cancel {
  position: absolute;
  top: 0;
  right: 5px;
  cursor: pointer;
}
.AddNewTodo form .confirm {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}
.AddNewTodo form .confirm button {
  width: 100%;
  height: 40px;
  background: #0080ff;
  color: white;
  font-size: 1.1rem;
  border-radius: 0 0 4px 4px;
}

.Calendar {
  padding: 1rem;
  border-bottom: 1px solid #ebebeb;
}
.Calendar .header {
  display: flex;
  align-items: center;
}
.Calendar .header .title {
  display: flex;
  align-items: center;
  flex: 1;
}
.Calendar .header .title p {
  margin-left: 5px;
  font-size: 1.1rem;
  font-weight: bold;
}
.Calendar .header .btns span {
  border-radius: 50%;
  cursor: pointer;
  width: 25px;
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.Calendar .header .btns span:hover {
  background-color: #0080ff;
  transition: all 300ms ease-in-out;
}
.Calendar .items {
  padding: 0.5rem;
}
.Calendar .items .item {
  padding: 0.25rem;
  margin: 5px;
  font-size: 1rem;
  cursor: pointer;
}

.Projects {
  padding: 1rem;
  border-bottom: 1px solid #ebebeb;
}
.Projects .header {
  display: flex;
  align-items: center;
  width: 210px;
}
.Projects .header .title {
  display: flex;
  align-items: center;
  flex: 1;
}
.Projects .header .title p {
  margin-left: 5px;
  font-size: 1.1rem;
  font-weight: bold;
}
.Projects .header .btns {
  display: flex;
  align-items: center;
}
.Projects .header .btns span .edit {
  margin-right: 5px;
}
.Projects .header .btns span {
  border-radius: 50%;
  cursor: pointer;
  width: 25px;
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.Projects .header .btns span:hover {
  background-color: #0080ff;
  transition: background 300ms ease-in-out;
}
.Projects .items {
  padding: 0.5rem;
}
.Project {
  position: relative;
  display: flex;
  align-items: center;
  padding: 0.25rem;
  margin: 5px;
  font-size: 1rem;
}
.Project .name {
  cursor: pointer;
  /*Текст может быть разорван в любом месте, даже внутри слова.
  Это полезно, когда нужно избежать горизонтальной прокрутки или выхода текста за пределы контейнера.*/
  word-break: break-all;
}
.Project .btns {
  display: flex;
  align-items: center;
}
.Project .btns .total-todos {
  position: absolute;
  right: 0;
  background: #ebebeb;
  width: 25px;
  height: 25px;
  font-size: 0.9rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}
.Project .btns .edit-delete {
  position: absolute;
  right: 0;
  display: flex;
}

.Project .btns .edit-delete span {
  border-radius: 50%;
  cursor: pointer;
  width: 25px;
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background 300ms ease-in-out;
}
.Project .btns .edit-delete span.edit:hover {
  background: #acffac;
}
.Project .btns .edit-delete span.delete:hover {
  background: #ffacac;
}

.AddNewProject .add-button span {
  margin-right: 5px;
}

.ProjectForm {
  background-color: white;
  padding: 1rem;
  border-radius: 4px;
  position: relative;
  padding-bottom: 40px;
}
.ProjectForm input {
  width: 100%;
  height: 40px;
  margin: 15px 0;
  border: none;
  font-size: 1.2rem;
}
.ProjectForm input:focus {
  outline: none;
  border-bottom: 1px solid #0080ff;
}
.ProjectForm button {
  position: absolute;
  bottom: 0;
  height: 40px;
  width: 50%;
  border: none;
  color: white;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: filter 300ms ease-in-out;
}
.ProjectForm button.cancel {
  left: 0;
  background-color: #bebebe;
}
.ProjectForm button.confirm {
  right: 0;
  background-color: #0080ff;
}
.ProjectForm button:hover {
  filter: brightness(1.1);
}

.Todos {
  width: 450px;
  height: 80%;
  background-color: white;
  position: absolute;
  bottom: 40px;
  left: 40px;
  border-radius: 4px;
  opacity: 0.9;
  padding: 0 0.5rem 0.5rem 0.5rem;
  overflow-y: auto;
}
.Todos .selected-project {
  margin: 1rem 0;
  text-transform: capitalize;
  font-size: 1.2rem;
  font-weight: bold;
  padding-bottom: 2px;
  border-bottom: 1px solid #eeeeee;
}
.Todos .todos{
  overflow-y: auto;
}
.Todo .todo-container .text{
  position: relative;
}

.Todo {
  padding: 0.5rem 1rem;
}
.Todo .todo-container {
  display: flex;
}
.Todo .todo-container .check-todo{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 15px;
}
.Todo .todo-container span {
  cursor: pointer;
}
.Todo .todo-container .text {
  position: relative;
  flex: 1;
  margin: 0 10px;
  cursor: pointer;
}
.Todo .todo-container .text span {
  font-size: 0.8rem;
  color: #bebebe;
}
/*Интерполяция стилей — это плавный переход между двумя значениями CSS-свойства с помощью анимации или трансформаций.
Она автоматически вычисляет промежуточные состояния между начальным и конечным значением, создавая smooth-эффект.*/
.Todo .todo-container .text .line {
  height: 1px;
  background: #bebebe;
  position: absolute;
  top: 25%;
  width: 0;
  transition: width 250ms ease-in;
}
.Todo .todo-container .text .line-through {
  width: 100%;
}

.Todo .todo-container .add-to-next-day {
  margin-right: 10px;
}
.EditTodo {
  position: absolute;
  bottom: 40px;
  left: calc(40px + 450px + 40px);
  background-color: white;
  height: 80%;
  width: 450px;
  border-radius: 4px;
  opacity: 0.9;
}

.EditTodo .header {
  margin: 1rem 0.5rem;

  font-size: 1.2rem;
  font-weight: bold;
  padding-bottom: 2px;
  border-bottom: 1px solid #eeeeee;
}

.Next7Days .day {
  margin: 5px;
  display: flex;
}

.Next7Days .day .name {
  font-weight: bold;
  font-size: 1rem;
}
.Next7Days .day .total-todos {
  margin-left: 10px;
}